import Handlebars from 'handlebars';

const clonedEscapeExpression = Handlebars.Utils.escapeExpression;

Handlebars.Utils.escapeExpression = function (string) {
  return clonedEscapeExpression(string).replace(/&#x3D;/g, '=');
};

export default Handlebars;



